.HoustonCard {
  max-width: 20%;
  height: auto;
  margin: 30px 30px 30px 40px;
  border: 6px solid #46211A;
  overflow: hidden;
  color: #46211A;
  width: 100%;
  object-fit: cover;
  display: block;
  position: relative;
  border-radius: 12px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.08);
  background: #111111;
  height: 80%;
  min-height: 0;
  cursor: pointer;
}

.CardImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    box-shadow: inset 0 8px 30px rgba(0,0,0,0.65), inset 0 -4px 10px rgba(255,255,255,0.02);
    transition: transform .35s ease, filter .35s ease;
}


.HoustonCard:focus {
  outline: none;
  transform: translateY(-2px);
}

.HoustonCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  box-shadow: inset 0 8px 30px rgba(0,0,0,0.65), inset 0 -4px 10px rgba(255,255,255,0.02);
  transition: transform .35s ease, filter .35s ease;
}

.HoustonCard:hover img,
.HoustonCard:focus img {
  transform: scale(1.03);
  filter: brightness(0.9);
}

.HoustonCard .overlay {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, #000000 0%, rgba(0,0,0,0.55) 60%);
  color: #ffffff;
  text-align: center;
  opacity: 0;
  transition: opacity .28s ease, transform .28s ease;
  transform: translateY(6px);
  padding: 16px;
}

.HoustonCard:hover .overlay,
.HoustonCard:focus .overlay,
.HoustonCard:focus-within .overlay {
  opacity: 1;
  transform: translateY(0);
}

.HoustonCard .overlay .title {
  font-size: 1.15rem;
  font-weight: 700;
  text-shadow: 0 2px 8px rgba(0,0,0,0.6);
}

.HoustonCard .overlay .subtitle {
  margin-top: 6px;
  font-size: .95rem;
  opacity: .95;
}

.projectsGrid > .HoustonCard { 
  border: 3px solid transparent; 
    }

.projectsGrid > .HoustonCard + .HoustonCard { 
  border-top-color: rgba(0,0,0,0.03); 
    }

.HoustonCard.project-1 .overlay { 
  background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(80,175,96,0.55) 60%); 
  }